import style from './Messeges.module.css'
import React from 'react';
import Messege from './Messege/Messege'
import People from './People/People'


const Messeges = ({DialogsPeople,DialogsPage,}) => {


    let Statement = DialogsPeople.map(people => (<People id={people.id} name={people.name} key={people.id}/>))
    let Statement1 = DialogsPage.map(messege => (
        <Messege id={messege.id} messege={messege.messege} key={messege.id}/>))


    return (
        <div className={style.messeges}>
            <div className={style.people}>
                {Statement}
            </div>
            <div className={style.messege}>

                <div>{Statement1}</div>
            </div>
        </div>
    )
}

export default Messeges;