import style from './Messege.module.css'
import React from "react";
import MessageFormContainer from "./MessageForm/MessageFormContainer";

const Messege = ({id,messege}) => {

    return (
        <div className={style.messeges}>
            <div className={style.Form}>

                <div className={style.FormInfo}>
                    <img
                        src="https://cdn.searchenginejournal.com/wp-content/uploads/2019/08/c573bf41-6a7c-4927-845c-4ca0260aad6b-760x400.jpeg"/>
                </div>
                <MessageFormContainer/>
                {"Name:   " + id}<br/>
                {"messege:  " + messege}
            </div>

        </div>
    )
}

export default Messege;